import React from "react";
import logo from "./new_LANC_logo.svg";
import "../App.css";

const Title = () => {
  return (
    <div>
      <img alt="lanclogo" src={logo} className="App-logo1" />
      <div
        style={{
          width: "75%",
          margin: "auto",
          fontSize: "larger",
          marginTop: "10px",
        }}
      >
        <h1>
          <b>Medical-Legal Partnership</b>
        </h1>
        <h2>
          <b>Referral Assessment Tool</b>
        </h2>
      </div>
    </div>
  );
};

export default Title;
