import React from "react";
import "../App.css";
import { Accordion } from "react-bootstrap";

const Glossary = () => {
  return (
    <div style={{ fontSize: "large" }}>
      <p style={{ margin: "25px", fontSize: "larger" }}>
        <b>Click on a question below to expand the answer:</b>
      </p>
      <Accordion className="FAQS" style={{ width: "50%", margin: "auto" }}>
        <Accordion.Item eventKey="0">
          <Accordion.Header>
            <b>What is Legal Aid of North Carolina (LANC)?</b>
          </Accordion.Header>
          <Accordion.Body>
            Legal Aid of North Carolina is a statewide, nonprofit law firm that
            provides free legal services in civil matters to low-income people
            in order to ensure equal access to justice and to remove legal
            barriers to economic opportunity. You can find more information
            about Legal Aid of North Carolina{" "}
            <a href="https://legalaidnc.org/about-us/mission">here</a>.
          </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="1">
          <Accordion.Header>
            <b>What is a Medical-Legal Partnership (MLP)?</b>
          </Accordion.Header>
          <Accordion.Body>
            As explained by the{" "}
            <a href="https://medical-legalpartnership.org/">
              National Center for Medical-Legal Partnerships
            </a>
            , "Medical-legal partnerships integrate the unique expertise of
            lawyers into health care settings to help clinicians, case managers,
            and social workers address structural problems at the root of so
            many health inequities."
            <br />
            <br />
            LANC MLP has local partnerships with medical institutions across
            North Carolina and with the NC Medicaid Healthy Opportunities Pilots
            (HOP) Program. Together with our partner health professionals, we
            seek to form an integrated care team that allows both medical and
            legal experts to work at the top of their licenses in helping to
            alleviate social determinants of health, as well as barriers to
            health equity.
          </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="2">
          <Accordion.Header>
            <b>
              What categories of legal problems can LANC help my patients with?
              What do they mean?
            </b>
          </Accordion.Header>
          <Accordion.Body>
            <p>LANC can assist with a wide range of civil legal problems:</p>
            <b>Benefits</b>
            <ul>
              <li>
                Issues such as food stamps, disability, health insurance,
                veteran's benefits.
              </li>
            </ul>
            <b>Housing</b>
            <ul>
              <li>
                Issues such as evictions, habitability, accomodations,
                foreclosure, and disaster relief.
              </li>
            </ul>
            <b>Consumer</b>
            <ul>
              <li>
                Issues such as harrassment by creditors, bankruptcy, and
                repossession of vehicles.
              </li>
            </ul>
            <b>Individual Rights</b>
            <ul>
              <li>
                Issues such as wills, power of attorneys, expunctions, and some
                limited immigration issues.{" "}
              </li>
            </ul>
            <b>Family</b>
            <ul>
              <li>
                Issues such as interpersonal/domestic violence, and custody when
                related to domestic violence.
              </li>
            </ul>
            <b>Education</b>
            <ul>
              <li>
                Issues such as access to special education, enrollment,
                discipline, and bullying/harrassment.
              </li>
            </ul>
            <b>Employment</b>
            <ul>
              <li>
                Issues involving migrant workers or unemployment insurance.
              </li>
            </ul>
          </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="3">
          <Accordion.Header>
            <b>
              What are common problems that LANC is unable to assist my patients
              with?
            </b>
          </Accordion.Header>
          <Accordion.Body>
            <b>Criminal Law</b>
            <br />
            LANC only assists with <i>civil</i> legal issues. We cannot help
            defend patients against criminal charges, including things like
            traffic tickets and DUIs. We also cannot give legal advice in these
            cases. <br />
            <br />
            However, we do assist with expunctions of those already convicted,
            since that is a civil law issue. To assess an expunction issue,
            select "Individual Rights" in the assessment below.
            <br />
            <br />
            <b>Immigration</b>
            <br />
            In some cases, LANC is limited in the assistance that it can offer
            undocumented patients. This is due to funding restrictions. There
            are notable exceptions, which include domestic violence, many
            migrant worker issues, and any issue where someone else in the
            household with a legal status is willing and able to be the client.
          </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="4">
          <Accordion.Header>
            <b>Is everyone that I refer eligible for help from Legal Aid?</b>
          </Accordion.Header>
          <Accordion.Body>
            LANC <i>does</i> have eligibility criteria that need to be met in
            order for us to assist your patients.
            <br />
            <br />
            <p>
              <b>
                As a referring health professional, you should not triage your
                referrals to LANC based on whether you think your patient is
                eligible. You should only refer based on whether LANC can work
                on the type of legal problem the patient is experiencing. LANC
                staff will make an eligibility determination during our intake
                call with the patient.
              </b>
            </p>
            Nonetheless, we will discuss our eligibility criteria here for your
            general knowledge.
            <br />
            <br />
            Aside from having a legal issue that we can work on, the
            patient/potential client must also: <br />
            <br />
            <ol>
              <li>
                Be under 187% of the federal poverty line, except for victims of
                domestic violence and assistance with the Affordable Care Act
                (ACA) marketplace.
              </li>
              <li>Have assests less than twice 187% FPL</li>
              <li>Have some sort of legal status</li>
              <li>
                Not have a conflict of interest that would prohibit
                representation due to the North Carolina Rules of Professional
                Conduct for attorneys
              </li>
            </ol>
            These conditions and the rules governing them are nuanced, and our
            intake staff are trained to navigate them. Never fail to refer a
            family because you think that they may be ineligible before they
            talk with a LANC MLP intake specialist.
          </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="5">
          <Accordion.Header>
            <b>
              Does LANC MLP have training materials for health professionals?
            </b>
          </Accordion.Header>
          <Accordion.Body>
            We do! You can find them here [link forthcoming].
          </Accordion.Body>
        </Accordion.Item>
      </Accordion>
    </div>
  );
};

export default Glossary;
