export function imgSelector(title) {
  if (title === "Health Insurance and Medicaid") {
    return "https://media.istockphoto.com/vectors/health-insurance-vector-illustration-medical-protection-medical-vector-id1136667774?k=20&m=1136667774&s=612x612&w=0&h=iJ-t7jGJPsDxQMwX_NnSzsPXqxCDOV7zDI-KT0xZBko=";
  } else if (title === "Disability (SSI/SSDI)") {
    return "https://media.istockphoto.com/vectors/american-security-card-with-its-social-security-number-vector-id1316476874?k=20&m=1316476874&s=612x612&w=0&h=cDl37W_5J4aVN8npGa39B-nR7ruP-f8ddb4wlA5XCuo=";
  } else if (title === "Food Insecurity") {
    return "data:image/jpeg;base64,";
  } else if (title === "WorkFirst") {
    return "https://www.pngitem.com/pimgs/m/101-1016980_3-hands-social-services-clipart-hd-png-download.png";
  } else if (
    title ===
    ("Eviction" ||
      "Conditions" ||
      "Loss of Housing Subsidy or Voucher" ||
      "Mortgage Foreclosure" ||
      "Disaster Relief" ||
      "Accommodations/Modifications for Disability" ||
      "Housing Discrimination" ||
      "Other Housing Issue Not Listed")
  ) {
    return "data:image/png;base64,";
  } else if (title === ("Debt Collection" || "Repossession of a Vehicle")) {
    return "data:image/jpeg;base64,";
  } else if (
    title ===
    ("Wills & Powers of Attorney" ||
      "Immigration" ||
      "Expunction" ||
      "Driver's License Restoration")
  ) {
    return "https://media.istockphoto.com/vectors/hand-holding-scales-solid-icon-concept-civil-rights-sign-on-white-vector-id1277251837?k=20&m=1277251837&s=612x612&w=0&h=FMdhs1-eE6_GiTdoR6sAS5GlnKQSE9hD0lkmfEHdJd4=";
  } else if (title === "Interpersonal/Domestic Violence") {
    return "https://ih1.redbubble.net/image.594262905.2484/flat,750x,075,f-pad,750x1000,f8f8f8.u1.jpg";
  } else if (title === "Custody") {
    return "https://pix4free.org/assets/library/2021-01-21/originals/child_custody_law.jpg";
  } else if (
    title ===
    ("Problem With Enrollment" ||
      "Suspension or Expulsion" ||
      "Students with Disabilities" ||
      "Discrimination Against a Student" ||
      "Bullying and Harassment")
  ) {
    return "https://i.pinimg.com/originals/9c/84/55/9c845519fece3b72977f0f885a0df851.png";
  } else if (title === ("Unemployment Insurance" || "Migrant Workers")) {
    return "clip-art-job-career-employment-vector-graphics-png-favpng-RbYG85GNcbhhW6tNKFW11xJa1.jpg";
  }
}
