import React from "react";
import { Modal, Button } from "react-bootstrap";
import { CLOSE_POPUP } from "./assessmentSlice";
import { useDispatch, useSelector } from "react-redux";

export default function IntermediateResult() {
  const dispatch = useDispatch();
  const popupCSS = useSelector((state) => state.assessment.present.popupCSS);
  const result = useSelector((state) => state.assessment.present.result);
  const getCategoryIndex = useSelector(
    (state) => state.assessment.present.currentCategoryIndex
  );
  // const getCleanResult = useSelector(
  //   (state) => state.assessment.present.fixedResult
  // );
  const allCategories = useSelector(
    (state) => state.assessment.present.selectedCategories
  );

  const getPrevCategory = allCategories.filter(
    (item) => allCategories.indexOf(String(item)) === getCategoryIndex - 1
  );

  const getResult = result.filter(
    (item) => result.indexOf(String(item)) === getCategoryIndex - 1
  );

  const getText = () => {
    if (
      getResult[0] !== undefined &&
      getResult[0].includes("Make a Referral") === true
    ) {
      return "Make a referral to MLP. See end results for more detailed information and resources.";
    } else if (
      getResult[0] !== undefined &&
      getResult[0].includes("Unable to Assist") === true
    ) {
      return "LANC MLP is unable to assist with this issue. Do not make a referral. See end results for more detailed information and other resources.";
    } else if (
      getResult[0] !== undefined &&
      getResult[0].includes("Consultation") === true
    ) {
      return "Please schedule a consultation with a LANC MLP staff member to discuss this issue. See end results for more information.";
    }
  };

  // const cleanedResult = String(getResult)
  //   .replace(/\\/g, "")
  //   .replaceAll("] ]", "")
  //   .replaceAll("[", "")
  //   .replaceAll('"', "");

  // const updateResult = dispatch(UPDATE_FIXED_RESULT(cleanedResult));
  // console.log(updateResult);

  return (
    <>
      <Modal
        show={popupCSS}
        centered={true}
        backdrop="static"
        style={{ textAlign: "center" }}
      >
        <Modal.Header style={{ margin: "auto" }}>
          <Modal.Title style={{ fontWeight: "bold" }}>
            Result for: <br />
            {getPrevCategory}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ fontSize: "larger", marginBottom: "10px" }}>
          {getText()}
        </Modal.Body>
        {/* above used to be {getCleanResult} */}
        <Button
          variant="primary"
          onClick={() => dispatch(CLOSE_POPUP())}
          style={{
            margin: "auto",
            textAlign: "center",
            padding: "10px",
            marginBottom: "10px",
            fontSize: "larger",
          }}
        >
          Proceed to Next Issue
        </Button>
      </Modal>
    </>
  );
}
