import { Button } from "react-bootstrap";
import React from "react";
// import logo from "./new_LANC_logo.svg";
import "../App.css";
import { UPDATE_TAB } from "./assessmentSlice.js";
import { useDispatch } from "react-redux";

const Header = () => {
  const dispatch = useDispatch();

  return (
    <div>
      {/* <img alt="lanclogo" src={logo} className="App-logo1" /> */}
      <div
        style={{
          width: "50%",
          margin: "auto",
          fontSize: "larger",
          marginTop: "25px",
        }}
      >
        {/* <h1>
          <b>Medical-Legal Partnership</b>
        </h1>
        <h2>
          <b>Referral Assessment Tool</b>
        </h2> */}
        {/* <br /> */}
        <div style={{ textAlign: "justify" }}>
          <p>
            This assessment tool was developed by Legal Aid of North Carolina's
            (LANC) Medical-Legal Partnership Program (MLP) to help health
            professionals:
          </p>
          <ol style={{ width: "100%", margin: "auto" }}>
            <li>Understand when to make a referral,</li>
            <li>Learn what LANC can do to assist their patients, and</li>
            <li>
              Access helpful information and resources to provide their
              patients, even if a referral is not appropriate.
            </li>
          </ol>
          <br />
          <p>
            At the completion of the assessment, all results will be listed at
            the bottom of the assessment tab. You can export these results for
            your records.
          </p>
          <p>
            Do you have thoughts about this assessment tool? Please consider
            sharing your feedback with us{" "}
            <a href="https://forms.office.com/Pages/ResponsePage.aspx?id=0TWofYd6lUCJz3d-0I9d--BEFY5RmV1NlHbvq69LxF5UM0lGUVFVSVRGWUpGSFFJRTM0VkQ5WjY0MS4u">
              <b>here</b>
            </a>
            .
          </p>
        </div>
      </div>
      <Button
        style={{ fontSize: "larger", marginTop: "25px" }}
        onClick={() => dispatch(UPDATE_TAB("start"))}
      >
        Start Your Assessment
      </Button>
    </div>
  );
};

export default Header;
