import React from "react";
import { Card, ListGroup, ListGroupItem } from "react-bootstrap";
import { useSelector } from "react-redux";
import { imgSelector } from "./EndPics";

const ResultsCard = (props) => {
  const resultsList = useSelector((state) => state.assessment.present.result);
  const resourcesList = useSelector(
    (state) => state.assessment.present.resources
  );
  const categoriesList = useSelector(
    (state) => state.assessment.present.selectedCategories
  );

  function findResources() {
    var getR = resourcesList[categoriesList.indexOf(String(props.cardTitle))];
    var getR2 = getR.children;
    var getR3 = [];
    getR3.push(getR2);

    return getR3.flat();
  }

  var filteredResults = null;
  var cleanResults1 = null;
  var cleanResults2 = null;
  var cleanResults3 = null;
  var cleanResults4 = null;

  const clean = () => {
    if (resultsList.length > 0) {
      filteredResults =
        resultsList[categoriesList.indexOf(String(props.cardTitle))];
      cleanResults1 = String(filteredResults).replaceAll("[", "");
      cleanResults2 = cleanResults1.replaceAll(" [", "");
      cleanResults3 = cleanResults2.replaceAll("];", ";");
      cleanResults4 = cleanResults3.replaceAll("]", "");
      return cleanResults4.split("; ");
    }
  };

  function generateList() {
    if (resultsList.length > 0 && clean()[0].includes("Make a Referral"))
      return (
        <ListGroup className="list-group-flush">
          <ListGroupItem
            key={Math.random()}
            style={{ backgroundColor: "#a0e8a3" }}
          >
            <b>Result:</b>
            <br /> {clean()[0]}
          </ListGroupItem>
          <ListGroupItem key={Math.random()}>
            <b>Your Action:</b>
            <br /> {clean()[1]}
          </ListGroupItem>
          <ListGroupItem key={Math.random()}>
            <b>How LANC Can Help:</b> <br /> {clean()[2]}
          </ListGroupItem>
          <ListGroupItem key={Math.random()}>
            <b>Other Resources:</b> <br />
            <ol>
              {findResources().map((item) => {
                return (
                  <li key={Math.random()}>
                    <a
                      href={item.children[0].title}
                      target="_blank"
                      rel="noreferrer"
                    >
                      {item.title}
                    </a>
                  </li>
                );
              })}
            </ol>
          </ListGroupItem>
        </ListGroup>
      );
    else if (resultsList.length > 0 && clean()[0].includes("Unable to Assist"))
      return (
        <ListGroup
          className="list-group-flush"
          style={{ backgroundColor: "#e8a0a0" }}
        >
          <ListGroupItem key={Math.random()}>
            <b>Result:</b>
            <br /> {clean()[0]}
          </ListGroupItem>
          <ListGroupItem key={Math.random()}>
            <b>Your Action:</b>
            <br /> {clean()[1]}
          </ListGroupItem>
          <ListGroupItem key={Math.random()}>
            <b>Why LANC Can't Assist:</b> <br /> {clean()[2]}
          </ListGroupItem>
          <ListGroupItem key={Math.random()}>
            <b>Other Resources:</b> <br />
            <ol>
              {findResources().map((item) => {
                return (
                  <li key={Math.random()}>
                    <a
                      href={item.children[0].title}
                      target="_blank"
                      rel="noreferrer"
                    >
                      {item.title}
                    </a>
                  </li>
                );
              })}
            </ol>
          </ListGroupItem>
        </ListGroup>
      );
    else if (resultsList.length > 0 && clean()[0].includes("Consultation"))
      return (
        <ListGroup
          className="list-group-flush"
          style={{ backgroundColor: "#fadca5" }}
        >
          <ListGroupItem key={Math.random()}>
            <b>Result:</b>
            <br /> {clean()[0]}
          </ListGroupItem>
          <ListGroupItem key={Math.random()}>
            <b>Your Action:</b>
            <br /> {clean()[1]}
          </ListGroupItem>
          <ListGroupItem key={Math.random()}>
            <b>What's a Consultation?:</b> <br /> {clean()[2]}
          </ListGroupItem>
          <ListGroupItem key={Math.random()}>
            <b>Other Resources:</b> <br />
            <ol>
              {findResources().map((item) => {
                return (
                  <li key={Math.random()}>
                    <a
                      href={item.children[0].title}
                      target="_blank"
                      rel="noreferrer"
                    >
                      {item.title}
                    </a>
                  </li>
                );
              })}
            </ol>
          </ListGroupItem>
        </ListGroup>
      );
  }

  return (
    <Card
      style={{
        width: "18rem",
        margin: "15px",
      }}
    >
      <Card.Img
        variant="top"
        src={imgSelector(props.cardTitle)}
        style={{
          height: "200px",
          width: "auto",
          objectFit: "cover",
          border: "1px solid black",
        }}
      />
      <Card.Body style={{ flex: "0" }}>
        <Card.Title
          style={{ height: "50px", margin: "auto", padding: "0px 0" }}
        >
          <b>{props.cardTitle}</b>
        </Card.Title>
      </Card.Body>
      {generateList()}
    </Card>
  );
};

export default ResultsCard;
