import React from "react";
import "bootstrap/dist/css/bootstrap.css";
import { useSelector } from "react-redux";
import "./Components.css";
import { Table } from "react-bootstrap";

const ChoiceTable = () => {
  const getArray = useSelector(
    (state) => state.assessment.present.hiddenchoices
  );

  return (
    <div>
      <Table striped bordered hover size="sm">
        <thead>
          <tr>
            <th>Issue</th>
            <th>Question</th>
            <th>Answer</th>
          </tr>
        </thead>
        <tbody>
          {getArray.map((item) => {
            return (
              <tr key={Math.random()}>
                {item.map((choice) => {
                  return <td key={Math.random()}>{choice}</td>;
                })}
              </tr>
            );
          })}
        </tbody>
      </Table>
    </div>
  );
};

export default ChoiceTable;
