import { useSelector } from "react-redux";
import { Button } from "react-bootstrap";
import Export from "./Export";
import NewWindow from "react-new-window";
import { UPDATE_CODE } from "./assessmentSlice";
import { useDispatch } from "react-redux";

const ResultsButtons = () => {
  const dispatch = useDispatch();

  const resultsCSS = useSelector(
    (state) => state.assessment.present.resultsCSS
  );

  const theCode = useSelector((state) => state.assessment.present.windowCode);

  const openWindow = () => {
    if (theCode > 0) {
      return (
        <div>
          <NewWindow>
            <Export />
          </NewWindow>
        </div>
      );
    }
  };

  return (
    <div style={resultsCSS}>
      <Button
        onClick={() => window.location.reload()}
        style={{ margin: "15px" }}
      >
        Start a New Assessment
      </Button>
      <br />
      <Button onClick={() => dispatch(UPDATE_CODE())}>
        Export Your Results as PDF
      </Button>
      <div key={theCode}>{openWindow()}</div>
    </div>
  );
};
export default ResultsButtons;
