import React from "react";
import ChoiceTable from "./ChoiceTable";
import ResultsBox from "./ResultsBox";
import { useReactToPrint } from "react-to-print";
import { useRef } from "react";

const Export = () => {
  const componentRef = useRef();
  const handleprint = useReactToPrint({ content: () => componentRef.current });

  var today = new Date();
  var date =
    today.getFullYear() + "-" + (today.getMonth() + 1) + "-" + today.getDate();
  var time =
    today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
  var dateTime = date + " " + time;

  return (
    <React.Fragment>
      <div
        style={{ margin: "auto", paddingTop: "30px", textAlign: "center" }}
        ref={componentRef}
        onLoad={handleprint}
      >
        <h1>MLP Referral Assessment Results</h1>
        <h2>Generated on {dateTime}</h2>
        <div style={{ margin: "20px" }}>
          <label
            style={{
              fontWeight: "bold",
              fontSize: "large",
              marginBottom: "20px",
            }}
          >
            Your Selections
          </label>
          <ChoiceTable />
        </div>
        <label
          style={{
            fontWeight: "bold",
            fontSize: "large",
            marginBottom: "20px",
          }}
        >
          Your Results
        </label>
        <ResultsBox />
      </div>
    </React.Fragment>
  );
};

export default Export;
